import React from "react";


/** Navigation Components */
import {
  createStackNavigator,
} from "@react-navigation/stack";


/** Screens */

import { AdShow,Category,Item,ItemsShow,Search,Checkout,CheckoutDone,Home } from '../views'
/** Stack Creator */
const Stack = createStackNavigator();

// ------- Constants -------//
import { Animations } from '../constants'


/** Render() */
export default function MainNavigation(props) {
  // variables
  const snapPoints = React.useMemo(() => [0, '90%'], []);
  return [
    <Stack.Navigator
      key={"Screens"}
      screenOptions={Animations.screenOptions}
      headerMode="float"
      animation="fade"
      initialRouteName={"Home"}
    >
      <Stack.Screen name="Home" component={Home} />
      <Stack.Screen name="AdShow" component={AdShow} />
      <Stack.Screen name="Category" component={Category} />
      <Stack.Screen name="ItemsShow" component={ItemsShow} />
      <Stack.Screen name="Search" component={Search} />
    
    </Stack.Navigator>
  ];
}
